import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { async } from '@angular/core/testing';
import { jqxGridComponent } from '../../../../shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';


@Component({
  selector: 'app-parking-manage-parking-slots',
  templateUrl: './parking-manage-parking-slots.component.html',
  styleUrls: ['./parking-manage-parking-slots.component.scss']
})
export class ParkingManageParkingSlotsComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  isDataLoaded:boolean = false;

  parkingSlotDataList:any;

  parkingSlotData:any;
  
  slotFieldType:string = "unitno";
  slotData:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  modalService:any;

  apartmentBlockUnitDataList:any;

  slotMainTypeDataList:any;
  slotTypeDataList:any;
  vehicleTypeDataList:any;
  parkingTypeList:any;


  public slotDataList: Array<any> = [];
  public slotHeader: any;
  filterData:string="";
  
  @HostListener('window:onEditTicket', ['$event.detail'])
  onEditTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let parkingSlotId = dataRecord.parkingSlotId;
    this.router.navigateByUrl('/ams/parking-management/edit-parking-slot/' + parkingSlotId);
  }

  
  @HostListener('window:onTicketDelete', ['$event.detail'])
  onTicketDelete(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let parkingSlotId = dataRecord.parkingSlotId
    this.modalService.showConfirmModal(parkingSlotId);
  }

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private parkingService: ParkingService,
    private apartmentService: ApartmentService,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private router : Router
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  searchSlotRate() {
    if(this.filterData != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.filterData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.slotHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'ParkingSlots');
  }
  
  ngOnInit() {
    this.getParkingSlot();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.slotHeader = [{
      text: 'slot name',
      datafield: 'slotName',
      cellsrenderer:cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'slot block no',
      datafield: 'parkingSlotBlockNumber',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'slot main type',
      datafield: 'slotMainTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'slot type',
      datafield: 'slotTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'vehicle type',
      datafield: 'parkingTypeName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'sq. m',
      datafield: 'sqrFt',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editTicketEvent(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEvent(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      minwidth: 120,
      renderer: columnrenderer
    }];

    //delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null){
        this.isDataLoaded = false;
        var params = {
          parkingSlotId: item,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }
        this.vehicleService.deleteParkingSlot(params).subscribe((res:any) => {
          this.getParkingSlot();
          // this.parkingSlotDataList = this.parkingSlotDataList.filter((type) => type.parkingSlotId !== item.parkingSlotId);
          // this.isDataLoaded = true;
        });
      }
    });
  }
  /**
   * get parking slot
   */
  getParkingSlot(){
    let parkingSlotParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.vehicleService.getParkingSlotByApartmentId(parkingSlotParams).subscribe((res:any) => {
      var rawSlotRateType = res.filter(data => {
        return data.isActive
      });
      let arrangeSlotRawData = {
        localdata: rawSlotRateType,
        datatype: "array"
      }
      this.slotDataList = new jqx.dataAdapter(arrangeSlotRawData);
    });
  }
}


function showConfirmDeleteEvent(row){
  var event = new CustomEvent('onTicketDelete', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;


function editTicketEvent(row){
  var event = new CustomEvent('onEditTicket', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editTicketEvent = editTicketEvent;